<template>
  <div class="login">
    <div class="login__cont">
      <div class="login__cont--row">
        <div class="item">
          <Input v-model="phone" type="text" placeholder="请输入手机号" autosize size="large" :maxlength="11" clearable autofocus />
          <!-- <input type="text" placeholder="请输入手机号" maxlength="11" v-model="phone"> -->
        </div>
        <div class="err">{{phoneErrTxt}}</div>
      </div>
      <div class="login__cont--row">
        <div class="item">
          <!-- <input type="text" placeholder="请输入验证码" v-model="validCode"> -->
          <Input v-model="validCode" type="text" placeholder="请输入验证码" size="large" clearable />
          <div class="code__btn" v-show="show" @click="getCode">验证码</div>
          <div class="code__btn" v-show="!show">{{count}} s</div>
        </div>
        <div class="err">{{codeErrTxt}}</div>
      </div>
      <div class="login__cont--btn" @click="login"></div>
    </div>
  </div>
</template>
<script>
import mixin from '../../mixins/mixins.js';
import { Local } from 'js/utils.js';

const TIME_COUNT = 60;
const REG_PHONE = /^1[3|4|5|7|8][0-9]{9}$/;
// const REG_PHONE = /^1\d{10}$/;
export default {
  name: 'login',
  props: [],
  data() {
    return {
      phoneErrTxt: '',
      codeErrTxt: '',
      show: true,
      count: 0,
      timer: null,
      phone: '',
      validCode: ''
    };
  },
  mixins: [mixin],
  created() {
    let that = this;
    let phone = Local.get('user_phone');
    if (phone && phone != '') {
      that.$router.push('/fillinfo');
    }
  },
  mounted() {

  },
  methods: {
    getCode() {
      let that = this;
      if (that.phone == '') {
        that.phoneErrTxt = '*手机号不能为空';
        return
      } else if (!REG_PHONE.test(that.phone)) {
        that.phoneErrTxt = '*手机号输入有误，请重新输入';
        return
      }
      that.phoneErrTxt = ''
      that.$api.SendCodeByPhone(that.phone).then((res) => {
        // console.log(res)
        if (res.code == 10000) {
          that.$toast('验证码发送成功，请注意查收');
          if (!that.timer) {
            that.count = TIME_COUNT;
            that.show = false;
            that.timer = setInterval(() => {
              if (that.count > 0 && that.count <= TIME_COUNT) {
                that.count--;
              } else {
                clearInterval(that.timer);
                that.show = true;
                that.timer = null;
              }
            }, 1000)
          }
        } else {
          that.$toast(res.msg);
        }
      })
    },
    login() {
      let that = this;
      if (that.phone == '') {
        that.phoneErrTxt = '*手机号不能为空';
        return
      } else if (!REG_PHONE.test(that.phone)) {
        that.phoneErrTxt = '*手机号输入有误，请重新输入';
        return
      } else {
        that.phoneErrTxt = '';
      }
      if (that.validCode == '') {
        that.codeErrTxt = '*验证码输入不能为空';
        return
      } else {
        that.codeErrTxt = '';
      }

      that.$api.Login(that.phone, that.validCode).then((res) => {
        if (Number(res.code) == 10000) {
          // console.log(res)
          that.$Message.success('登录成功');
          Local.set('user_id', res.data.id)
          Local.set('user_phone', res.data.phone)
          setTimeout(() => {
            that.$router.push('/fillinfo');
          }, 500)
        } else {
          that.$Message.warning(res.msg);
        }
      })
    },
  },
};

</script>
<style lang="scss" scoped>
.login {
  position: relative;
  min-height: 100vh;
  background: url('~img/index_bg.png') center center / 100% 100% no-repeat;

  &__cont {
    position: absolute;
    left: 50%;
    bottom: 9%;
    transform: translateX(-50%);
    width: 80%;
    height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &--row {

      .item {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        /deep/ .ivu-input-wrapper {
          .ivu-input-icon {
            height: 41px;
            line-height: 41px;
          }

          input {
            width: 100%;
            height: 41px;
            background: #FFFBF7;
            border-radius: 4px;
            font-size: 16px;
            padding: 0 12.5px;
            border: 0;
            outline: none;

            ::placeholder {
              color: #CEC9CD;
            }
          }
        }


        .code__btn {
          width: 100px;
          height: 41px;
          flex-shrink: 0;
          line-height: 41px;
          margin-left: 15px;
          text-align: center;
          border-radius: 4px;
          font-size: 16px;
          font-weight: 500;
          color: #D5C8A7;
          // background: linear-gradient(0deg, rgba(215, 194, 124, 1), rgba(192, 147, 108, 1));
          border: 1px solid #D5C8A7;

          // box-shadow: 0px 2px 1px 0px rgba(213, 200, 167, 1);
          &:active {
            opacity: .5;
          }
        }
      }

      .err {
        height: 25px;
        line-height: 25px;
        font-size: 13px;
        color: #E84E39;
      }
    }

    &--btn {
      width: auto;
      height: 53.5px;
      margin-top: 10px;
      transition: all .2s ease-in-out 0s;
      background: url('~img/index_btn_zccs.png') center center / 100% 100% no-repeat;

      &:active {
        opacity: .5;
        transform: scale(1.1);
      }
    }

  }
}

</style>



